import { useRef, useState } from "react";
import CodeMirror from '@uiw/react-codemirror';
import './style.css';

const typeEnum = {
  STRING: 'STRING',
  BYTES: 'BYTES',
  INTEGER: 'INTEGER',
  FLOAT: 'FLOAT',
  NUMERIC: 'NUMERIC',
  BIGNUMERIC: 'BIGNUMERIC',
  BOOLEAN: 'BOOLEAN',
  TIMESTAMP: 'TIMESTAMP',
  DATE: 'DATE',
  TIME: 'TIME',
  DATETIME: 'DATETIME',
  GEOGRAPHY: 'GEOGRAPHY',
  JSON: 'JSON'
}

const modeEnum = {
  NULLABLE: 'NULLABLE',
  REQUIRED: 'REQUIRED',
  REPEATED: 'REPEATED'
}

const SchemaEditor = () => {
  const seperator = ';';

  const [schema, setSchema] = useState([]);
  const [metrics, setMetrics] = useState('id;name;date');

  const convert = () => {
    const metricsSplit = metrics.split(seperator);
    setSchema([]); //clear to prevent code appendend

    metricsSplit.map(metric => {
      if (metric.length <= 0)
        return;

      setSchema((currentSchema) => ([...currentSchema, createColumn(metric)]));
    })
  }

  const createColumn = (name, type, mode, maxLength) => {
    return {
      name,
      type,
      mode,
      maxLength //FOR: STRING, BYTES
    };
  }

  const renderOutput = () => {
    return JSON.stringify(schema, null, 2);
  }

  return (
    <div className="editor">
      <div className="input-toolbar">
        <span>metric without space seperate by <b><code>{seperator}</code></b></span>
        <button onClick={convert}>convert</button>
      </div>
      <div className="output-toolbar">
        <span>Output:</span>
      </div>
      <div className="input-wrapper">
        <CodeMirror
          value={metrics}
          onChange={(value) => (setMetrics(value))}
          style={{ "height": "100%" }}
        />
      </div>
      <div className="output-wrapper">
        <CodeMirror
          value={renderOutput()}
          editable={false}
          style={{ "height": "100%" }}
        />
      </div>
    </div>
  );
}

export default SchemaEditor;